@import url(https://rsms.me/inter/inter-ui.css);
@font-face {
  font-family: 'Camphor';
  font-weight: 500;
  src: url(/meteorite/static/media/Camphor-Regular.5bcaa4d6.ttf);
}
@font-face {
  font-family: 'Camphor';
  font-weight: 300;
  src: url(/meteorite/static/media/Camphor-Light.fa08cd9c.ttf);
}
@font-face {
  font-family: 'Camphor';
  font-weight: 700;
  src: url(/meteorite/static/media/Camphor-Heavy.4cb7266d.ttf);
}

::-moz-selection {
  color: #fff;
  background: #24292e;
}

::selection {
  color: #fff;
  background: #24292e;
}

html, body, * {
  font-family: 'Inter UI', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  color: #202124;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background: #ffffff;
  margin: 0;
  padding: 0;
}

h1 {
  font-weight: 500;
  font-size: 48px;
  letter-spacing: -0.05px;
  font-size: 52px;
  letter-spacing: -0.75px;
}

h2 {
  font-weight: 500;
  font-size: 38px;
  letter-spacing: -0.05px;
}

h3 {
  font-weight: 500;
  font-size: 28px;
  letter-spacing: -0.05px;
  margin: 10px 0 20px;
}

p {
  font: 15px/22px 'Inter UI', system-ui, sans-serif;
  color: #111;
  font: 15px/22px 'Inter UI', system-ui, sans-serif;
  font-size: 15px;
  line-height: 1.5;
  letter-spacing: -0.002em;
  font-weight: 400;
  margin: 20px 0;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-overflow-scrolling: touch;
  scroll-behavior: smooth;
  -webkit-font-kerning: normal;
          font-kerning: normal;
  -ms-font-feature-settings:     "calt" 1, "kern" 1, "liga" 1;
  -o-font-feature-settings:      "calt" 1, "kern" 1, "liga" 1;
  -webkit-font-feature-settings: "calt" 1, "kern" 1, "liga" 1;
  font-feature-settings:         "calt" 1, "kern" 1, "liga" 1, "kern";
}

.react-tooltip {
  z-index: 999999;
  pointer-events: none;
  position: absolute;
  background: #242a31;
  color: #fff;
  padding: 4px 8px;
  font-weight: 600;
  font-size: 11px;
  border-radius: 4px;
  white-space: nowrap;
  -webkit-transform: translateX(-35%) translateY(-15px);
          transform: translateX(-35%) translateY(-15px);
  opacity: 0;
  -webkit-transition: all 100ms ease-in;
  transition: all 100ms ease-in;
}


.container-gradient {
  /* background: radial-gradient(farthest-corner at -0% 100%, #7247ff 30%, #00ffbe 95%); */
  /* background: radial-gradient(farthest-corner at -0% 100%, #6772e5 30%, #00cfff 95%); */
  background: -webkit-radial-gradient(-0% 100%, farthest-corner, #24292e 30%, #213a54 95%);
  background: radial-gradient(farthest-corner at -0% 100%, #24292e 30%, #213a54 95%);
  background-size: 400% 400%;

  -webkit-animation: gradientTransition 20s ease infinite;
  animation: gradientTransition 20s ease infinite;
}

.button-container a {
  text-align: center;
  box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
  margin: 0 auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
  /* color: #6772e5; */
  color: #24292e;
  background: #fff;
  border-radius: 4px;
  align-items: center;
  padding: 0 16px;
  height: 48px;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  -webkit-text-decoration: none;
  text-decoration: none;
  -webkit-transition: all 0.12s ease-in-out;
  transition: all 0.12s ease-in-out;
  display: inline-flex;
}
.button-container a:hover {
  background: #f9f9f9;
  box-shadow: 0 2px 6px #4a4a4a5c;
}
.button-container a:active {
  background: #e4e4e4;
  box-shadow: 0 0 0 #4a4a4a5c;
}

.button-container-alt a {
  position: relative;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 0 4px 6px rgba(50,50,93,.11), 0 1px 3px rgba(0,0,0,.08);
  margin: 0 auto;
  cursor: pointer;
  /* color: #6772e5; */
  color: #24292e;
  background: #fff;
  border-radius: 4px;
  align-items: center;
  padding: 0 16px;
  height: 48px;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  -webkit-text-decoration: none;
  text-decoration: none;
  -webkit-transition: all 0.04s ease-in-out;
  transition: all 0.04s ease-in-out;
  display: inline-flex;
}
.button-container-alt a:before {
  content: "";
  -webkit-transition: all 150ms ease;
  transition: all 150ms ease;
  background: rgba(190, 197, 208, 0.25);
  border-radius: 4px;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
}
.button-container-alt a:hover:before {
  -webkit-transform: scale(1);
          transform: scale(1);
}
.button-container-alt a:active:before {
  background: rgba(190, 197, 208, 0.5)
}


@-webkit-keyframes gradientTransition {
  0% {background-position: 0% 50%}
  50% {background-position: 60% 50%}
  100% {background-position: 0% 50%}
}
@keyframes gradientTransition {
  0% {background-position: 0% 50%}
  50% {background-position: 60% 50%}
  100% {background-position: 0% 50%}
}

